import { _decorator, Component, Node, SpriteFrame, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('game_jxlw')
export class game_jxlw extends Component {

    //赢分、我的积分、比倍 用的数字
    @property(SpriteFrame)
    up_number_spf: SpriteFrame[] = [];

    //下注用的数字
    @property(SpriteFrame)
    dowm_number_spf: SpriteFrame[] = [];

    //赢分spr
    @property(Sprite)
    win_score: Sprite[] = [];

    //我的积分spr
    @property(Sprite)
    user_score: Sprite[] = [];

    bet_score = []


    start() {
        //初始化分数
        this.setUserScore(globalThis.userMgr.score);
        this.setWinScore(0);
    }

    update(deltaTime: number) {


    }

    //设置赢分
    setWinScore(score: number) {
        let str = score.toString();
        let len = str.length;
        for (let i = 0; i < 10; i++) {
            if (i < len) {
                let st = Number(str[len - i - 1]);
                this.win_score[i].spriteFrame = this.up_number_spf[st];
                this.win_score[i].node.active = true;
            }
            else {
                this.win_score[i].node.active = false;
            }
        }
    }

    //设置用户分数
    setUserScore(score: number) {
        let str = score.toString();
        let len = str.length;
        for (let i = 0; i < 10; i++) {
            if (i < len) {
                let st = Number(str[len - i - 1]);
                this.user_score[i].spriteFrame = this.up_number_spf[st];
                this.user_score[i].node.active = true;
            }
            else {
                this.user_score[i].node.active = false;
            }
        }
    }

    onBtnClick(event: any, customEventData: any) {
        switch (customEventData) {
            case 'go':
                {
                    this.setUserScore(10000)
                    break;
                }
            case 'bet1':
                {
                    break;
                }
            default:
                break;
        }
    }

    //下注分数
    setBetScore()
    {

    }
}